<template>
    <Myheader></Myheader>
    <div id="bk">
        <div class="main">
            <h1>地区</h1>
            <div id="regional_list">
                <router-link
                    v-for="(region,index) in regional"
                    :key="index"
                    :to="'/RegionInfo/'+region.formerly"
                >
                    <div
                        class="regional_card"
                        :style="{'background-image': 'url(' + require(`../assets/img/regional_posters/${region.posters}`) + ')',
                        'background-size': 'cover'}"
                    >
                        <div class="mb"></div>
                        <img
                            :src="require(`../assets/img/crest_icon/${region.icon}`)"
                            alt=""
                        >
                        <div class="foot">
                            <p>{{ region.translation }}</p>
                        </div>
                    </div>
                </router-link>
            </div>
        </div>
    </div>
    <myfooter></myfooter>
</template>
<script>
import { reactive, toRefs } from "vue";
import axios from "axios";
import Myfooter from "../components/footer.vue";
import Myheader from "../components/header.vue";
export default {
    name: "regionals_showcase",
    components: {
        Myfooter,
        Myheader,
    },
    setup() {
        const data = reactive({
            regional: [],
        });
        //这里存放数据
        return {
            ...toRefs(data),
        };
    },
    //监听属性 类似于data概念
    computed: {},
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        fetchRegional() {
            axios
                .get("http://localhost:8089/api/region")
                .then((response) => {
                    this.regional = response.data;
                })
                .catch((error) => {
                    console.error(error);
                });
        },
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {},
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
        this.fetchRegional();
    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang='less' scoped>
@import url(../assets/css/Clear_style.css);
@import url(../assets/css/regional_showcase.less);
</style>